@import url('https://fonts.googleapis.com/css?family=Neuton|News+Cycle&display=swap');
@import url('https://fonts.googleapis.com/css?family=Frank+Ruhl+Libre|IBM+Plex+Sans|Lora|Nunito|Old+Standard+TT|Playfair+Display|Source+Sans+Pro&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
